:root {
    /* Light */
    --background-color-light: #f1f1f1;
    --text-color-light: #333333;
    --color-light: rgb(0, 188, 56);
    /* Dark */
    --background-color-dark:#333333;
    --text-color-dark: #ffffff;
    --color-dark: rgb(255,215,0);
    /* Application */
    --primary-color: #000000;
    --background-color: #000000;
    --text-color: #000000;
    /* RGB */
    --rgb-primary-light: 175, 0, 108;
    --rgb-primary-dark: 255, 215, 0;
    --rgb-primary: 0, 0, 0;
    --rgb-background-light: 241, 241, 241;
    --rgb-background-dark: 51, 51, 51;
    --rgb-background: 0, 0, 0;
    
}