.projects {
    padding: 4rem 0;
  }
  
  .projects h2 {
    font-size: 2.5rem;
    margin-bottom: 2rem;
    text-align: center;
  }
  
  .project-grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
    gap: 2rem;
  }
  
  .project-card {
    background-color: var(--primary-color);
    border: solid 3px transparent;
    border-radius: 8px;
    overflow: hidden;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
    /*transition: all 0.3s;*/
    cursor: pointer;
  }
  
  .project-card:hover {
    transform: translateY(-5px);
    box-shadow: 15px 16px 15px var(--primary-color);
    border: solid 3px var(--primary-color);
  }
  
  .project-card img {
    width: 100%;
    height: 200px;
    object-fit: cover;
  }
  
  .project-info {
    padding: 1.5rem;
  }
  
  .project-info h3 {
    font-size: 1.3rem;
    margin-bottom: 0.5rem;
  }
  
  .project-info p {
    font-size: 0.9rem;
    color: var(--text-color);
  }